import LandingArt from "../../../assets/dead_background_website.png";
import {Link} from "react-router-dom";
import DownloadIcon from "../../../assets/download-icon.svg";
import PlayCircle from "../../../assets/play-circle.svg";

import "./Play.css"


function Play() {
    return (
        <div>
            <div className="video-player game-player">
                <iframe width="970" height="652" src="bb_webgl/index.html"
                        id="game" title="bb-trailer-video" frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        allowFullScreen/>
            </div>
            <div className="container landing-content play-page">
                <div className="landing-content__item">
                    <a href="https://echonox.itch.io/bbs-delivery-service?secret=7JmMIVS0kJz72U2lg0fMOpncSw">
                        <div style={{textAlign: 'center'}} id="landing-play-now-button">
                            Play on itch.io
                        </div>
                    </a>
                </div>
                <div className="landing-content__item">
                    <h1><b>Controls</b></h1>
                    Left and Right Arrow Keys - Move
                    <br/>
                    C - Jump
                    <br/>
                    X - Magnetize or Throw Package
                    <br/>
                    Space - Continue dialogue
                </div>
                <div className="landing-content__item">
                    <h1><b>Download Instructions</b></h1>
                    <div style={{width: 'fit-content'}}>
                        <a href="B.B.'s Delivery Service.zip" download>
                            <div className="download-button">
                                Download Zip File <img src={DownloadIcon} alt="download"/>
                            </div>
                        </a>
                    </div>
                    1. Download the zip file
                    <br/>
                    2. Unzip the file
                    <br/>
                    Run the B.B.’s Delivery Service.exe file to play!
                </div>
                <div className="landing-content__item">
                    <h1><b>Sources</b></h1>
                    <br/>
                    <b> Background Music: </b> <a href="https://www.newgrounds.com/audio/listen/690406">Abandoned</a> and <a href="https://www.newgrounds.com/audio/listen/598558">Hadel Zone</a> by <a href="https://1f1n1ty.newgrounds.com/">1finity</a>
                    <br/>
                    <b> Sound Effects: </b> <a href="https://mixkit.co/">Mixkit free sound effect library</a>
                    <br/>
                    <b> In-game Pixel Font: </b> <a href="https://assetstore.unity.com/packages/2d/fonts/free-pixel-font-thaleah-140059"> Thaleah </a>
                    <br/>
                    <b> Falling Leaves Effect: </b> <a href="https://assetstore.unity.com/packages/vfx/particles/falling-leaves-vfx-pack-93023"> Falling Leaves VFX Pack </a>
                </div>
            </div>
        </div>
    )
}

export default Play;